import React from 'react';
import connect from '@vkontakte/vk-connect';
import {Div,Root,View,Panel} from '@vkontakte/vkui';

const API = 'd6b1b0d9d6b1b0d9d6b1b0d9bfd6dfbf39dd6b1d6b1b0d98892e1f9fd9dfc20298a3e5f';

class App extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      count:0,
      fetchedUser:{}

    }
  }

  componentDidMount(e){
    connect.subscribe((e) => {
      switch (e.detail.type) {
        case 'VKWebAppGetUserInfoResult':
          this.setState({ fetchedUser: e.detail.data });
          break;
        default:
          console.log(e.detail.type);
      }
    });

    connect.send('VKWebAppGetUserInfo', {});

  }


  render(){
	return(
   <View id="game" activePanel={'game'}>
     <Panel id='game'>
     {this.state.count}
     </Panel>
   </View>

)
 }
}

export default App;
